___CSS_LOADER_EXPORT___.push([module.id, `.carousel-container {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: auto;
  aspect-ratio: 16 / 5; /* Menjaga rasio proporsional pada desktop */
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.carousel-slide {
  position: absolute;
  inset: 0;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  z-index: 0;
}

.carousel-slide::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.4); /* Atur tingkat kegelapan di sini */
  z-index: 1;
}

.carousel-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  z-index: 0;
  position: relative;
}

.carousel-slide.active {
  opacity: 1;
  z-index: 1;
}

/* Arrow Navigation */
.carousel-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  border: none;
  padding: 10px;
  font-size: 20px;
  cursor: pointer;
  border-radius: 50%;
  z-index: 10;
  transition: background-color 0.3s ease;
}
.carousel-arrow:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
.carousel-arrow.left {
  left: 10px;
}
.carousel-arrow.right {
  right: 10px;
}

/* Dots */
.carousel-dots {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
  z-index: 10;
}
.carousel-dot {
  width: 12px;
  height: 12px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.carousel-dot.active {
  background-color: white;
}

/* Responsive Carousel untuk Mobile */
@media (max-width: 1024px) {
  .carousel-container {
    aspect-ratio: unset;
    height: 400px; /* tinggi tetap untuk mobile */
  }

  .carousel-arrow {
    padding: 6px;
    font-size: 16px;
  }

  .carousel-dots {
    bottom: 10px;
    gap: 6px;
  }

  .carousel-dot {
    width: 10px;
    height: 10px;
  }
}

@media (max-width: 480px) {
  .carousel-container {
    height: 340px; /* lebih pendek di hp kecil */
  }

  .carousel-arrow {
    padding: 5px;
    font-size: 14px;
  }

  .carousel-dots {
    bottom: 8px;
    gap: 5px;
  }

  .carousel-dot {
    width: 8px;
    height: 8px;
  }
}
